@import './libs/reset';
@import './fonts/fonts';

body {
  background: #FFF;
  color: #030303;
  font-variant-numeric: lining-nums proportional-nums;
  font-family: 'Raleway', sans-serif;
  font-size: 15px;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
}

.button {
  border-radius: 8px;
  border: 1px solid rgba(19, 164, 189, 0.25);
  padding: 8px 12px;
  cursor: pointer;
  transition: background .3s ease-in-out, color .3s ease-in-out, border-color .3s ease-in-out;

  &:hover {
    background: #13a3bdc2;
    color: #FFF;
    border-color: transparent;
  }

  &_active {
    background: #13A4BD;
    color: #FFF;
    border-color: transparent;
  }
}

.input {
  border-radius: 8px;
  border: 1px solid rgba(19, 164, 189, 0.25);
  background: #FFF;
  width: 200px;
  padding: 6px 10px;
  outline: none;
}