*,
*::before,
*::after {
  box-sizing: border-box
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
figure,
blockquote,
dl,
dd {
  margin: 0
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0
}

body,
figure,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  padding: 0
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5
}

body {
  -ms-overflow-style: none;
  scrollbar-width: thin
}

body::-webkit-scrollbar {
  display: none
}

a {
  color: inherit;
  text-decoration: none
}

button,
input,
optgroup,
select,
textarea {
  font: inherit;
  margin: 0;
  color: inherit;
  background-color: transparent;
  border: none
}

textarea {
  overflow: auto
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  line-height: 1.5
}

table {
  border-collapse: collapse;
  width: 100%
}

th,
td {
  padding: 0;
  text-align: left
}

img {
  border-style: none
}

img {
  max-width: 100%;
  height: auto
}