.app {
  &__wrapper {
    display: grid;
    width: 100%;
    height: 100vh;
    place-items: center;
  }

  &__view {
    width: 70%;
    min-height: 80%;
    border-radius: 12px;
    background: #faf9fa;
    box-shadow: 2px 0px 2px 0px rgba(0, 0, 0, 0.03), 0px 0px 16px 0px rgba(0, 0, 0, 0.02);
    padding: 24px 16px;
  }

  &__header {
    display: flex;
    justify-content: center;
    align-items: center;

    &> :first-child {
      margin-right: 50px;
    }
  }

  &__form-fields {
    margin-top: 32px;
    padding: 0 24px;

    &__field {
      margin-top: 12px;

      &:first-of-type {
        margin-top: 0;
      }
    }

    .row {
      display: flex;
      align-items: center;

      .row__label {
        margin: 0;
        &:first-of-type {
          margin-right: 26px;
        }
      }
    }

    &__footer {
      display: flex;
      justify-content: center;
      margin-top: 40px;
    }
  }

  &__label {
    color: rgba(#171618, .5);
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-bottom: 6px;
  }

  &__answer {
    margin-top: 50px;

    &__wrapper {
      background: #fff;
      border-radius: 12px;
      box-shadow: 2px 0px 2px 0px rgba(0, 0, 0, 0.03), 0px 0px 16px 0px rgba(0, 0, 0, 0.02);
      padding: 16px;
      min-height: 155px;
    }

    &__result {
      font-size: 17px;
      line-height: 24px;
    }
  }
}

@media (max-width: 992px) {
  .app {
    &__wrapper {
      padding: 0 15px;
    }
    &__view {
      width: 100%;
    }  
  }
}

@media (max-width: 576px) {
  .app {
    &__wrapper {
      padding: 20px 10px;
      place-items: start;
      height: auto;
    }
    &__form-fields {
      padding: 0;
    }

    .custom-select {
      width: 100%;
    }

    .input {
      width: 100%;
    }
  }
}