.custom-select {
  width: 150px;
  position: relative;

  &__header {
    border-radius: 8px;
    border: 1px solid rgba(19, 164, 189, 0.25);
    background: #FFF;
    padding: 6px 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;

    img {
      width: 14px;
    }
  }

  &__options {
    position: absolute;
    width: 100%;
    border-radius: 8px;
    border: 1px solid rgba(19, 164, 189, 0.25);
    background: #FFF;
    margin-top: 4px;
    overflow: hidden;
    padding: 4px 0;
    z-index: 1;

    li {
      position: relative;
      padding: 6px 10px;
      cursor: pointer;

      &::after {
        content: '';
        position: absolute;
        left: 9px;
        bottom: 0px;
        width: 0;
        height: 1px;
        background: transparent;
        transition: width .5s;
      }

      &:hover {
        &::after {
          background: #13A4BD;
          width: 30%;
        }
      }
    }
  }
}